import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import { blogsApi, stayConnectedApi, downloadApi } from "../../apis/services";
import Spinner from "../Spinner";
import { Pagination } from "antd";
import { successNotification } from "../Toast";
import { useFormik } from "formik";
import * as Yup from "yup";
import { errorResponseHandler } from "../../apis/http";
import pdfFile from "./pdf.pdf";
import CryptoJS from "crypto-js";


const Blog = () => {
    const [allData, setAllData] = useState([]);
    const [loading, setLoading] = useState(true);
    const [allCategory, setAllCategory] = useState({});
    const [selectedCategory, setSelectedCategory] = useState(null);
    const [currentPage, setCurrentPage] = useState(1);
    const pageSize = 6;
    const [initially, setInitially] = useState(true)

    const navigate = useNavigate();

    const encryptId = (id) => {
        return encodeURIComponent(
            CryptoJS.AES.encrypt(id.toString(), "mySecretKey").toString()
        );
    };

    const fetchAllBlogs = async () => {
        try {
            const response = await blogsApi();
            setAllCategory(response?.category_counts || {});
            setAllData(response?.blogs || []);
        } catch (error) {
            errorResponseHandler(error);
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        fetchAllBlogs();
    }, []);

    const validationSchema = Yup.object({
        email: Yup.string()
            .email("Please enter a valid email address")
            .required("Email is required"),
    });

    const formik = useFormik({
        initialValues: { email: "" },
        validationSchema,
        onSubmit: async (values, { resetForm, setSubmitting }) => {
            try {
                const formData = new FormData();
                formData.append("email", values.email);

                const res = await stayConnectedApi(formData);
                if (res) {
                    successNotification("Email Added Successfully");
                }
                resetForm();
            } catch (error) {
                errorResponseHandler(error);
            } finally {
                setSubmitting(false);
            }
        },
    });



    const decoderValidationSchema = Yup.object({
        name: Yup.string().required("Name is required"),
        email: Yup.string()
            .email("Enter a valid email")
            .required("Email is required"),
    });

    const decoderFormik = useFormik({
        initialValues: { name: "", email: "" },
        validationSchema: decoderValidationSchema,
        onSubmit: async (values, { resetForm, setSubmitting }) => {
            try {
                const formData = new FormData();
                formData.append("name", values.name);
                formData.append("email", values.email);

                await downloadApi(formData);
                successNotification("Email added successfully");

                const link = document.createElement("a");
                link.href = pdfFile;
                link.download = "pdf.pdf";
                link.click();
                resetForm();
            } catch (error) {
                errorResponseHandler(error);
            } finally {
                setSubmitting(false);
            }
        },
    });




    if (loading) {
        return <Spinner />;
    }

    const handleCategoryClick = (category) => {
        setSelectedCategory(category === selectedCategory ? null : category);
        setCurrentPage(1);
    };

    const filteredBlogs = selectedCategory
        ? allData.filter(blog => blog.category === selectedCategory)
        : allData;

    const startIndex = (currentPage - 1) * pageSize;
    const paginatedBlogs = filteredBlogs.slice(startIndex, startIndex + pageSize);

    return (
        <>
            <input type="checkbox" id="mpclose" />
            {initially && (
                <div className="mpfix"> <div className="mpbody"> <label htmlFor="mpclose" onClick={() => { setInitially(false) }}> <svg viewBox="0 0 320 512"> <path d="M193.94 256L296.5 153.44l21.15-21.15c3.12-3.12 3.12-8.19 0-11.31l-22.63-22.63c-3.12-3.12-8.19-3.12-11.31 0L160 222.06 36.29 98.34c-3.12-3.12-8.19-3.12-11.31 0L2.34 120.97c-3.12 3.12-3.12 8.19 0 11.31L126.06 256 2.34 379.71c-3.12 3.12-3.12 8.19 0 11.31l22.63 22.63c3.12 3.12 8.19 3.12 11.31 0L160 289.94 262.56 392.5l21.15 21.15c3.12 3.12 8.19 3.12 11.31 0l22.63-22.63c3.12-3.12 3.12-8.19 0-11.31L193.94 256z"></path> </svg> </label> <div className="kt-popup-newsletter"> <div className="popup-title"> <h1>Stay Connected With Us</h1> <span className="primary"> Don't miss our latest newsletters, tips, and updates. <br /> Enter your email below to get new content delivered right to your inbox. </span> </div> <form className="form-subscribe" onSubmit={formik.handleSubmit} noValidate> <input className="input required email" type="email" name="email" placeholder="Your email address" value={formik.values.email} onChange={formik.handleChange} onBlur={formik.handleBlur} id="mce-EMAIL" /> {formik.touched.email && formik.errors.email && (<div className="text-red-500 text-sm mt-1" style={{ color: "red" }}> {formik.errors.email} </div>)} <button className="button" id="subscribe" type="submit" disabled={formik.isSubmitting} > {formik.isSubmitting ? "Submitting..." : "Sign Me Up"} </button> </form> <div className="checkbox-bottom"> <input id="checkBox" type="checkbox" /> <label htmlFor="checkBox"> We respect your privacy. No spam, ever. </label> </div> </div> </div> </div>

            )}
            <div className="page-header bg-section dark-section">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-12">
                            <div className="page-header-box">
                                <h1 className="text-anime-style-3" data-cursor="-opaque">Blogs</h1>
                                <nav className="wow fadeInUp">
                                    <ol className="breadcrumb">
                                        <li className="breadcrumb-item"><Link to="/">home </Link></li>
                                        <li className="breadcrumb-item"><i className="fa fa-chevron-right" aria-hidden="true"></i></li>
                                        <li className="breadcrumb-item active" aria-current="page">Blogs</li>
                                    </ol>
                                </nav>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            {/* Blog Section */}
            <div className="page-blog pb-0 pt-2">
                <div className="container">
                    <div class="disclaimer-banner">
                        <p><strong>Disclaimer:</strong> Educational content only. Not medical advice. Always consult healthcare professionals for medical concerns.</p>
                    </div>
                    <div className="row">
                        {/* Main Blog Content */}
                        <div className="col-lg-8">
                            <div className="row">
                                {paginatedBlogs.map((value, index) => {
                                    const encryptedId = encryptId(value.id);
                                    return (
                                        <div className="col-lg-6 col-md-6" key={index}>
                                            <div className="post-item wow fadeInUp">
                                                <div className="post-featured-image">
                                                    <Link to={`/blog-detail/${encryptedId}`} data-cursor-text="View">
                                                        <figure className="image-anime">
                                                            <img src={value.image} alt={value.title} />
                                                        </figure>
                                                    </Link>
                                                </div>

                                                <div className="post-item-body">
                                                    <div className="post-meta">
                                                        <span>
                                                            <i className="fas fa-user"></i> {value.author}
                                                        </span>
                                                    </div>

                                                    <div className="post-meta gap-4">
                                                        <span>
                                                            <i className="far fa-calendar"></i>{" "}
                                                            {new Date(value.created_at).toLocaleDateString("en-US", {
                                                                day: "2-digit",
                                                                month: "short",
                                                                year: "numeric",
                                                            })}
                                                        </span>
                                                        <span>
                                                            <i className="far fa-clock"></i> 7 min read
                                                        </span>
                                                    </div>

                                                    <div className="post-item-content">
                                                        <h3
                                                            className="blog-title-sec"
                                                            style={{ color: "black", fontWeight: "bold" }}
                                                        >
                                                            <Link to={`/blog-detail/${encryptedId}`}>
                                                                {value.title.length > 40
                                                                    ? value.title.slice(0, 40) + "..."
                                                                    : value.title}
                                                            </Link>
                                                        </h3>

                                                        <p
                                                            className="post-excerpt"
                                                            dangerouslySetInnerHTML={{
                                                                __html:
                                                                    (value.description_1 || "")
                                                                        .slice(0, 100) + "...",
                                                            }}
                                                        />
                                                    </div>

                                                    <div className="post-item-btn">
                                                        <Link
                                                            to={`/blog-detail/${encryptedId}`}
                                                            className="readmore-btn"
                                                        >
                                                            Read more
                                                        </Link>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    );
                                })}


                                {/* ✅ AntD Pagination (Centered) */}
                                {
                                    filteredBlogs.length > pageSize && (
                                        <div className="col-lg-12 d-flex justify-content-center mt-4">
                                            <Pagination
                                                current={currentPage}
                                                pageSize={pageSize}
                                                total={filteredBlogs.length}
                                                onChange={(page) => setCurrentPage(page)}
                                                className="custom-pagination"
                                            />
                                        </div>
                                    )
                                }
                            </div>
                        </div>

                        {/* Sidebar */}
                        <div className="col-lg-4">
                            <aside className="sidebar">
                                <div className="sidebar-widget">
                                    <h3>Categories</h3>
                                    <ul className="categories-list">
                                        <li key="all">
                                            <a
                                                className={!selectedCategory ? "active" : ""}
                                                onClick={e => { e.preventDefault(); handleCategoryClick(null); }}
                                                style={{ cursor: "pointer" }}
                                            >
                                                All <span>({Object.values(allCategory).reduce((a, b) => a + b, 0)})</span>
                                            </a>
                                        </li>
                                        {Object.entries(allCategory).map(([category, count]) =>
                                            count > 0 && (
                                                <li key={category}>
                                                    <a
                                                        className={selectedCategory === category ? "active" : ""}
                                                        onClick={e => { e.preventDefault(); handleCategoryClick(category); }}
                                                        style={{ cursor: "pointer" }}
                                                    >
                                                        {category} <span>({count})</span>
                                                    </a>
                                                </li>
                                            )
                                        )}
                                    </ul>
                                </div>
                                <div className="email-capture-1">
                                    <h3 className="text-white mb-3">Get Our Food Decoder Guide</h3>
                                    <p>Download our free PDF guide to understanding food labels and making healthier choices.</p>

                                    <form className="email-form" onSubmit={decoderFormik.handleSubmit} noValidate>
                                        <input
                                            type="text"
                                            name="name"
                                            placeholder="Your Name"
                                            value={decoderFormik.values.name}
                                            onChange={decoderFormik.handleChange}
                                            onBlur={decoderFormik.handleBlur}
                                        />
                                        {decoderFormik.touched.name && decoderFormik.errors.name && (
                                            <div style={{ color: "red" }}>{decoderFormik.errors.name}</div>
                                        )}

                                        <input
                                            type="email"
                                            name="email"
                                            placeholder="Your Email"
                                            value={decoderFormik.values.email}
                                            onChange={decoderFormik.handleChange}
                                            onBlur={decoderFormik.handleBlur}
                                        />
                                        {decoderFormik.touched.email && decoderFormik.errors.email && (
                                            <div style={{ color: "red" }}>{decoderFormik.errors.email}</div>
                                        )}

                                        <button
                                            type="submit"
                                            className="btn text-black bg-white m-auto rounded-5 mt-3"
                                            disabled={decoderFormik.isSubmitting}
                                        >
                                            {decoderFormik.isSubmitting ? "Submitting..." : "Download Guide"}
                                        </button>
                                    </form>
                                </div>


                            </aside>
                        </div>

                    </div>
                </div>
            </div >

            <style>{`
    /* Highlight active category */
    .categories-list li a.active {
        font-weight: bold;
        color: #007bff;  /* ✅ Bootstrap blue */
    }

    /* Center pagination */
    .custom-pagination {
        display: flex;
        justify-content: center;
    }
        
`}</style>

        </>
    );
};

export default Blog;
